<template>
  <div class="imgHead">
    <div class="bg">
      <picture>
        <source srcset="../../assets/img/bg_mb.jpg" media="(max-width: 767px)">
        <img src="../../assets/img/bg.jpg" >
      </picture>
    </div>
    <div class="top">
      <div class="flexItem" @click="changeTab">
        <!--        <img class="btnImg" src="../assets/img/navBtn.png" />-->
        <Icon class="close" :style="styleClose" type="md-add" size="30" color="white"/>
        <Icon class="menu" :style="styleMenu" type="md-menu" size="30" color="white"/>
      </div>
      <ul class="itemTab" :style="ulHeight">
        <li v-for="item in tabs" :key="item.id" :style="liHeight">
          <a :href=item.href><Icon :custom=item.icon color="white" size="20"/><span class="h3">{{item.nav}}</span></a>
        </li>
      </ul>
    </div>
    <div class="des">
      <div class="container">
        <Row>
          <Col :xs="24" :sm="24">我有个梦——中国梦</Col>
        </Row>
      </div>
    </div>
  </div>
</template>

<script>
import topData from '../../include/top'

// let itemTab = document.getElementsByClassName('app')

export default {
  name: topData.name,
  data () {
    return {
      tabs: topData.tabs,
      theme1: 'light',
      styleClose: {
        opacity: 0
      },
      styleMenu: {
        opacity: 1
      },
      ulHeight: {
        height: 0
      },
      liHeight: {
        height: 0
      },
      toggleTab: true
    }
  },
  methods: {
    changeTab: function () {
      if (this.toggleTab) {
        this.styleMenu = 'opacity: 0'
        this.styleClose = 'opacity: 1; transform: rotate(45deg);'
        this.ulHeight = 'height: 300px'
        this.liHeight = 'height: 16%'
      } else {
        this.styleClose = 'transform: rotate(0deg); opacity: 0;'
        this.styleMenu = 'opacity: 1;'
        this.ulHeight = 'height: 0'
        this.liHeight = 'height: 0'
      }
      this.toggleTab = !this.toggleTab
      // console.log(itemTab.style.height)
    }
  }
}
</script>

<style scoped lang="scss">
@import './css/imgBg.scss';
</style>
